
body, ul, li, p, h1, h2, input, form, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
a {
  text-decoration: none;
}

/*=================侧边栏=================*/
.sidebar {
  width: 187px;
  height: 397px;
  display: block;
  position: fixed;
  top: 300px;
  right: 3%;
  background: url(../images/sidebar.png);
  z-index: 10;
}
.sidebar-hong {
  cursor: pointer;
  width: 187px;
  line-height: 26px;
  display: block;
  text-align: center;
  font-size: 23px;
  padding: 35px 0 17px 0;
  color: #f3dfb2;
}
.all-in-one-sidebar {
  width: 106px;
  height: 240px;
  display: block;
  margin: 0 auto;
}
.all-in-one-sidebar li a {
  cursor: pointer;
  width: 106px;
  height: 30px;
  display: block;
  background: url(../images/cbk.jpg) no-repeat;
  margin-top: 4px;
  text-align: center;
  line-height: 30px;
  color: #ed4360;
}

/*=====隐藏返回顶部========*/
.bottom-top {
  cursor: pointer;
  width: 120px;
  height: 50px;
  display: block;
  margin: 0 auto;
  display: none;
  background: url(../images/bottom-top.png) no-repeat;
  position: relative;
  top: -1px;
  right: -1px;
}
.goTop {
  cursor: pointer;
  width: 100px;
  height: 50px;
  display: block;
  text-align: center;
  line-height: 50px;
  color: #f3dfb2;
}

/*动画效果*/
.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}
@keyframes swing {
20% {
    transform: rotate3d(0, 0, 1, 15deg);
}
40% {
    transform: rotate3d(0, 0, 1, -10deg);
}
60% {
    transform: rotate3d(0, 0, 1, 5deg);
}
80% {
    transform: rotate3d(0, 0, 1, -5deg);
}
to {
    transform: rotate3d(0, 0, 1, 0deg);
}
}
.swing {
  transform-origin: top center;
  animation-name: swing;
}





